.CloneAssessmentMain {
  width: 1086px;
  background: #fff;
  margin: 20px auto;
  border-radius: 9px;
  padding: 20px;
  height: 90%;
  .innerDiv {
    width: 805px;
  }
  .assessment-name {
    font-size: 11px;
    padding-bottom: 10px;
    font-weight: 600;
  }
  .assessment-name-input-div .MuiInputBase-root {
    height: 34px;
    font-size: 11px;
  }
  .assessment-name-input-div .MuiOutlinedInput-input {
    padding: 2px 8px;
    font-size: 11px;
  }
  .industry-type-select-div {
    [class$="-control"] {
      font-size: 11px;
      background-color: #f8f8f8;
      font-weight: 500;
      color: #000;
    }
  }
  .save-continue-button-div {
    margin-top: 20px;

    .save-continue-button {
      width: 224px !important;
      font-size: 13px !important;
    }
  }
  .error {
    color: red;
    font-size: 11px;
    height: 20px;
  }
}
